@import 'general';

div.page-container {
  margin: 0;
}

body {
  margin: 0;
}

.about-container {
  display: flex;
  color: $textColor;

  img {
    margin: 40px 30px 30px 70px;
    display: inline-block;
    height: 300px;
  }

  .self-description-container {
    width: 600px;
    font-family: $textFont;
    margin:30px 30px 10px 10px;
    //line-height: 27px;
    font-size: 14px;
    display: inline-block;

    .paragraph {
      margin-top: 4px;
      margin-bottom: 10px;
    }

    .semibold {
      font-weight: 600;
    }

    .my-name {
      font-family: $titleFont;
      font-size: 30px;
      font-weight: 600;
    }

    a {
      @include link();
    }
  }
}

.research-section {
  color: $textColor;
  margin-top: 30px;

  .section-title {
    @include section-title();
  }

  .publication_preview {
    display: flex;
    margin: 10px 20px;
    font-family: $textFont;
    font-size: 14px;

    .thumbnail {
      align-self: left;
      img {
        width: 200px;
      }
    }

    .descriptions {
      margin: 5px 10px 10px 30px;
      align-self: right;
      .title {
        font-weight: 700;
      }

      .author {
        b {
          font-weight: 600;
        }
      }

      .conference {
        div {
          display: inline;
          font-style: italic;
        }
      }

      .description {
        margin-top: 16px;
      }

      .links {
        margin-top: 10px;
        a {
          margin-right: 15px;
          padding: 3px 10px;
          background: darkgray;
          border-radius: 4px;

          font-family: $titleFont;
          text-decoration: none;
          color: white;

          &:hover {
            background: $highlightBlockColor;
          }
        }
      }
    }
  }
}

.awards-section {
  margin-top: 20px;
  color: $textColor;

  .section-title {
    @include section-title();
  }

  .awards {
    max-width: 600px;
    margin: 10px 20px;
    font-family: $textFont;
    font-size: 14px;

    .award-preview {
      margin-bottom: 14px;

      .title {
        font-weight: 600;

        a {
          text-decoration: none;
          color: $textColor;
          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}
